<template>
  <div class="page-container">
    <h1 class="title">demo</h1>
    <div class="content">
      <el-upload
        class="upload-demo"
        action="https://jsonplaceholder.typicode.com/posts/"
        :auto-upload="false"
        ref="upload"
        :multiple="false"
        :on-change="upload"
        :file-list="fileList">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">xlsxlsx</div>
      </el-upload>
      <el-row class="input-ctrl">
        <el-col :span="6"></el-col>
        <el-col :span="18">
          <el-input  v-model="level" placeholder=""></el-input>
        </el-col>
      </el-row>
      <el-row class="input-ctrl">
        <el-col :span="6"></el-col>
        <el-col :span="18">
          <el-input  v-model="name" placeholder=""></el-input>
        </el-col>
      </el-row>
      <div>
        <el-button type="primary" @click="getExport"></el-button>
        <el-button  @click="reset"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
import XLSX from 'xlsx'
export default {
  data () {
    return {
      // todo
      tmpData: [], // 
      fileList: [],
      level: 2,
      uploading: false,
      name: 'new'
    }
  },
  methods: {
    reset () {
      this.tmpData = []
      this.fileList = []
    },
    upload(file, fileList) {
      if (fileList.length > 1) {
        this.$message.error('')
        this.fileList = []
        return false
      }
      this.uploading = true
      let files = {0:file.raw}
      this.readExcel1(files);
    },
    readExcel1(files) {//
            var that = this;
            if(files.length<=0){//
                return false;
            }else if(!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())){
                this.$Message.error('xlsxlsx');
                return false;
            }
    
            const fileReader = new FileReader();
            fileReader.onload = (ev) => {
                try {
                    const data = ev.target.result;
                    const workbook = XLSX.read(data, {
                        type: 'binary'
                    });
                    const wsname = workbook.SheetNames[0];//
                    const result = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]);//json
                    result.sort((a, b) => {
                      if (a[''] && b['']) {
                        const aR = a[''].split('-')
                        const bR = b[''].split('-')
                        let aStr = this.getStr(aR)
                        let bStr = this.getStr(bR)
                        const len = aStr.length - bStr.length
                        if (len >= 0) {
                          bStr = bStr.padEnd(aStr.length,'0');
                        } else {
                          aStr = aStr.padEnd(bStr.length,'0');
                        }
                        return aStr - bStr
                      } else {
                        return true
                      }
                    })
                   this.tmpData = result
                   this.uploading = false
                } catch (e) {
                  console.log(e)
                  this.uploading = false
                  this.$message.error('')
                    return false;
                }
            };
            fileReader.readAsBinaryString(files[0]);
    },
    getExport () {
      if (this.uploading) {
        this.$message.error('!')
        return
      }
      if (this.tmpData.length === 0) {
        this.$message.error('')
        return false
      }
      this.getLevelTotal(JSON.parse(JSON.stringify(this.tmpData)), parseInt(this.level))
    },
    getStr(arr) {
      if (arr.length > 3) {
        return arr.slice(0, 3).join('')
      } else {
        return arr.join('')
      }
    },
    getLevelTotal (data, n) {
      const outputData = []
      data.forEach(item => {
        const code = item['']
        if (code) {
          const len = outputData.length
          if (len === 0) {
            outputData.push({
              '': code.split('-').slice(0, n).join('-'),
              '': item[''],
              '': item['']
            })
          } else {
            const lastData = outputData[len - 1]
            const lastArr = lastData[''].split('-')
            const nowArr = code.split('-')
            // 
            let flagInd = 0
            let targetStr = []
            const  length = lastArr.length - nowArr.length > 0 ? nowArr.length : lastArr.length
            for (let i = 0; i < length; i++) {
              if (i < n) {
                if (lastArr[i] === nowArr[i]) {
                  flagInd++
                  targetStr.push(lastArr[i])
                } else {
                  break
                }
              } else {
                break
              }
            }
            if (flagInd === n) {
              outputData[len - 1][''] += item['']
              outputData[len - 1][''] += item['']
              outputData[len - 1][''] = targetStr.join('-')
            } else {
              outputData.push({
                '': code.split('-').slice(0, n).join('-'),
                '': item[''],
                '': item['']
              })
            }
          }
        }
      })
      this.downLoad(outputData, this.name)
    },
    downLoad (json, name) {
      var data = new Array();
      var keyArray = new Array();

      for (const key1 in json) {
          if (json.hasOwnProperty(key1)) {
              const element = json[key1];
              var rowDataArray = new Array();
              for (const key2 in element) {
                  if (element.hasOwnProperty(key2)) {
                      const element2 = element[key2];
                      rowDataArray.push(element2);
                      if (keyArray.length < this.getLength(element)) {
                          keyArray.push(key2);
                      }
                  }
              }
              data.push(rowDataArray);
          }
      }
      data.splice(0, 0, keyArray);
      const ws = XLSX.utils.aoa_to_sheet(data);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "SheetJS");
      /* generate file and send to client */
      XLSX.writeFile(wb, name + ".xlsx");
    },
    /**
 * @description: map
 * @param {Object} obj map 
 * @return: map
 */
    getLength (obj) {
      var count = 0;
      for (var i in obj) {

          if (obj.hasOwnProperty(i)) {
              count++;

          }
      }
      return count;
    }
  }
}
</script>
<style lang="scss" scoped>
.page-container {
  .title {
    text-align: center;
  }
  .content {
    text-align: center;
    margin: 40px auto;
    width: 500px;
    background: #f2f2f2;
    padding: 20px;
    border-radius: 15px;
    .input-ctrl {
      margin: 30px 0;
      line-height: 40px;
    }
  }
}
</style>


